import React, { useContext } from 'react'
import { ScrumboardProvider, ScrumboardContext } from './ScrumboardContext'
import Board from './Board';
import { DragDropContext, Droppable } from 'react-beautiful-dnd';
import { PlusOutlined } from '@ant-design/icons';
import { Avatar } from 'antd';
import reorder, { reorderQuoteMap } from './reoreder'
import { memberIds } from './ScrumboardData';
import ModalForm from './ModalForm';
import { modalModeTypes, createCardObject, AssigneeAvatar } from './utils';
import axios from 'axios';
import { Scrollbars } from 'react-custom-scrollbars';
import { connect } from 'react-redux';

const ScrumboardWrapper = props => {
	const {
		ordered,
		columns,
		modal,
		cardData,
		currentListId,
		modalMode,
		updateOrdered,
		updateColumns,
		updateModal,
		updateCurrentListId
	} = useContext(ScrumboardContext)

	const onDragEnd = result => {
		if (result.combine) {
			if (result.type === 'COLUMN') {
				const shallow = [...ordered];
				shallow.splice(result.source.index, 1);
				updateOrdered(shallow)
				return;
			}

			const column = columns[result.source.droppableId];
			const withQuoteRemoved = [...column];
			withQuoteRemoved.splice(result.source.index, 1);
			const newColumns = {
				...columns,
				[result.source.droppableId]: withQuoteRemoved,
			};
			updateColumns(newColumns)
			return;
		}

		if (!result.destination) {
			return;
		}

		const source = result.source;
		const destination = result.destination;

		if (
			source.droppableId === destination.droppableId && source.index === destination.index
		) {
			return;
		}

		if (result.type === 'COLUMN') {
			const newOrdered = reorder(
				ordered,
				source.index,
				destination.index,
			);
			updateOrdered(newOrdered)
			return;
		}
		const data = reorderQuoteMap({
			quoteMap: columns,
			source,
			destination,
		});
		updateColumns(data.quoteMap)
	}

	const onCloseModal = () => {
		updateModal(false)
	}

	const onModalSubmit = (values, mode) => {
		const data = columns
		if (mode === modalModeTypes(0)) {
			let newCard = createCardObject()
			newCard.name = values.cardTitle ? values.cardTitle : 'Untitled Card'
			data[currentListId].push(newCard)
			updateColumns(data)
			updateModal(false)
			updateCurrentListId('')
		}

		if (mode === modalModeTypes(1)) {
			const updatadedCard = data[currentListId].map(elm => {
				if (values.id === elm.id) {
					elm = values
				}
				return elm
			})
			data[currentListId] = updatadedCard
			updateColumns(data)
			updateModal(false)
		}

		if (mode === modalModeTypes(2)) {
			data[values.boardTitle ? values.boardTitle : 'Untitled Board'] = [];
			const newOrdered = [...ordered, ...[values.boardTitle ? values.boardTitle : 'Untitled Board']]
			let newColumns = {}
			newOrdered.forEach(elm => {
				newColumns[elm] = data[elm]
			});
			updateColumns(newColumns)
			updateOrdered(Object.keys(newColumns))
			updateModal(false)
		}
	}

	return (
		<>
			<DragDropContext>
				{props.containerHeight ? (
					<div className="scrumboard">
						<BoardWrapper {...props} />
					</div>
				) : (
						<BoardWrapper {...props} />
					)}
			</DragDropContext>
			<ModalForm
				visible={modal}
				onClose={() => onCloseModal()}
				onModalSubmit={(values, modalMode) => onModalSubmit(values, modalMode)}
				modalMode={modalMode}
				cardData={cardData}
				listId={currentListId}
			/>
		</>
	)
}

const BoardWrapper = ({ containerHeight, useClone, isCombineEnabled, withScrollableColumns, user_id }) => {
	const { ordered, columns, updateModal, updateModalMode } = useContext(ScrumboardContext);

	let [forwarded, setForwarded] = React.useState([]);
	let [progress, setProgress] = React.useState([]);
	let [completed, setCompleted] = React.useState([]);
	let [sent, setSent] = React.useState([]);
	let [billing, setBilling] = React.useState([]);
	let [paid, setPaid] = React.useState([]);
	let [edit, setEdit] = React.useState([]);

	const onAddBoardModal = () => {
		updateModal(true)
		updateModalMode(modalModeTypes(2))
	}

	const getForwardedDeals = async () => {
		try {
			let res = await axios.post('/scrumboardforwardeddetails', { user_id });
			setForwarded(res.data.data);
		} catch (error) {
			console.log(error);
		}
	}
	const getProgressDeals = async () => {
		try {
			let res = await axios.post('/scrumboardassigneddetails', { user_id });
			setProgress(res.data.data);
		} catch (error) {
			console.log(error);
		}
	}
	const getCompletedDeals = async () => {
		try {
			let res = await axios.post('/scrumboardompleteddetails', { user_id });
			setCompleted(res.data.data);
		} catch (error) {
			console.log(error);
		}
	}
	const getSentDeals = async () => {
		try {
			let res = await axios.post('/scrumboardsentdetails', { user_id });
			setSent(res.data.data);
		} catch (error) {
			console.log(error);
		}
	}
	const getBillingDeals = async () => {
		try {
			let res = await axios.post('/scrumboardbillingdetails', { user_id });
			setBilling(res.data.data);
		} catch (error) {
			console.log(error);
		}
	}
	const getPaidDeals = async () => {
		try {
			let res = await axios.post('/scrumboardpaiddetails', { user_id });
			setPaid(res.data.data);
		} catch (error) {
			console.log(error);
		}
	}
	const getEditDeals = async () => {
		try {
			let res = await axios.post('/scrumboardeditdetails', { user_id });
			setEdit(res.data.data);
		} catch (error) {
			console.log(error);
		}
	}

	React.useEffect(() => {
		getForwardedDeals();
		getProgressDeals();
		getCompletedDeals();
		getSentDeals();
		getBillingDeals();
		getPaidDeals();
		getEditDeals();
	}, []);

	return (
		<Droppable
			droppableId="board"
			type="COLUMN"
			direction="horizontal"
			ignoreContainerClipping={containerHeight}
			isCombineEnabled={isCombineEnabled}
		>
			{(provided) => (
				<div className="scrumboard" ref={provided.innerRef} {...provided.droppableProps}>
					<div className="scrumboard-header">
						<div>
							<h3>Order Status</h3>
						</div>
					</div>
					<Scrollbars className="scrumboard-body">
						<>
							<Board
								key={Math.random().toString()}
								index={0}
								title={'Forwarded Deals'}
								contents={forwarded}
								isScrollable={withScrollableColumns}
								isCombineEnabled={isCombineEnabled}
								useClone={useClone}
							/>
							<Board
								key={Math.random().toString()}
								index={0}
								title={'In Progress'}
								contents={progress}
								isScrollable={withScrollableColumns}
								isCombineEnabled={isCombineEnabled}
								useClone={useClone}
								progress={true}
							/>
							<Board
								key={Math.random().toString()}
								index={0}
								title={'Completed'}
								contents={completed}
								isScrollable={withScrollableColumns}
								isCombineEnabled={isCombineEnabled}
								useClone={useClone}
							/>
							<Board
								key={Math.random().toString()}
								index={0}
								title={'Sent to Client'}
								contents={sent}
								isScrollable={withScrollableColumns}
								isCombineEnabled={isCombineEnabled}
								useClone={useClone}
							/>
							<Board
								key={Math.random().toString()}
								index={0}
								title={'Edit by Client'}
								contents={edit}
								isScrollable={withScrollableColumns}
								isCombineEnabled={isCombineEnabled}
								useClone={useClone}
							/>
							<Board
								key={Math.random().toString()}
								index={0}
								title={'Fowarded to Billing'}
								contents={billing}
								isScrollable={withScrollableColumns}
								isCombineEnabled={isCombineEnabled}
								useClone={useClone}
							/>
							<Board
								key={Math.random().toString()}
								index={0}
								title={'Paid Deals'}
								contents={paid}
								isScrollable={withScrollableColumns}
								isCombineEnabled={isCombineEnabled}
								useClone={useClone}
							/>
						</>
						{provided.placeholder}
					</Scrollbars>
				</div>
			)}
		</Droppable>
	)
}

const Scrumboard = props => {
	return (
		<ScrumboardProvider>
			<ScrumboardWrapper {...props} user_id={props.user_id} />
		</ScrumboardProvider>
	)
}

const mapStateToProps = state => ({
	user_id: state.auth.user.user_id
})

export default connect(mapStateToProps)(Scrumboard);